import React, { useEffect, useState } from "react";
import CommonHero from "../../shared/CommonHero";
import { Button, Container, Grid, Typography } from "@mui/material";
import { Box } from "@mui/system";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getNewsData } from "../../redux/reducers/site";

const News = () => {
  const dispatch = useDispatch();
  const { news } = useSelector(({ site }) => site);
  const [showMore, setShowMore] = useState(false);
  const newsLength = news.length;

  const handleShowMore = () => {
    setShowMore(!showMore);
  };

  useEffect(() => {
    dispatch(getNewsData());
  }, []);
  newsLength &&
    news?.sort((a, b) => {
      return a.id - b.id;
    });
  return (
    <>
      <CommonHero title="Новости" />
      <Box
        sx={{
          padding: {
            md: "80px 15px 120px",
            sm: "40px 15px 120px",
            xs: "40px 15px 100px",
          },
          bgcolor: "#F6F7F9",
        }}
      >
        <Container maxWidth="xl">
          <Grid container className="custom-row">
            {news.length &&
              (news.length >= 10
                ? news
                    ?.slice(0,!showMore ? 8 : news.length)
                    .sort()
                    .reverse()
                    .filter(
                      (item) =>
                        item.category != "Акции" && item.category != "Полезное"
                    )
                    .map((article, id) => (
                      <Grid
                        sx={{
                          "&:last-child": {
                            display: { md: "block", xs: "none" },
                          },
                        }}
                        item
                        xs={12}
                        sm={6}
                        lg={4}
                        className="custom-col"
                        key={id}
                        style={{ marginBottom: 50 }}
                      >
                        <Link
                          to={`/news/${article.id}`}
                          sx={{ textDecoration: "none", color: "#000" }}
                        >
                          <img
                            src={article.border_photo}
                            alt={article.title}
                            style={{
                              marginBottom: 20,
                              borderRadius: 6,
                              objectFit: "cover",
                              width: "100%",
                              height: "216px",
                            }}
                          />
                          <Typography
                            variant="h6"
                            sx={{
                              marginBottom: "13px",
                              fontSize: { md: "20px", xs: "18px" },
                            }}
                          >
                            {article.title}
                          </Typography>
                          <Typography
                            variant="body2"
                            fontWeight={300}
                            sx={{
                              marginBottom: "15px",
                              /*height: { md: "44px", sm: "60px", xs: "40px" }*/
                              overflow: "hidden",
                              fontSize: { md: "16px", xs: "14px" },
                            }}
                            dangerouslySetInnerHTML={{
                              __html:
                                article.description.substring(0, 100) + "...",
                            }}
                          />

                          <Grid
                            container
                            alignItems="center"
                            justifyContent="space-between"
                          >
                            <Typography
                              variant="btnPrimaryOval"
                              sx={{
                                padding: {
                                  md: "5px 21px",
                                  sm: "4px 21px",
                                  xs: "5px 21px",
                                },
                                fontSize: {
                                  md: "16px",
                                  sm: "14px",
                                  xs: "16px",
                                },
                              }}
                            >
                              {article.category}
                            </Typography>
                            <Typography
                              variant="body2"
                              fontWeight={600}
                              sx={{
                                fontSize: {
                                  md: "16px",
                                  sm: "14px",
                                  xs: "16px",
                                },
                              }}
                            >
                              {new Date(article.create_at).toLocaleDateString()}
                            </Typography>
                          </Grid>
                        </Link>
                      </Grid>
                    ))
                : news
                    ?.sort()
                    .reverse()
                    .filter(
                      (item) =>
                        item.category != "Акции" && item.category != "Полезное"
                    )
                    .map((article, id) => (
                      <Grid
                        sx={{
                          "&:last-child": {
                            display: { md: "block", xs: "none" },
                          },
                        }}
                        item
                        xs={12}
                        sm={6}
                        lg={4}
                        className="custom-col"
                        key={id}
                        style={{ marginBottom: 50 }}
                      >
                        <Link
                          to={`/news/${article.id}`}
                          sx={{ textDecoration: "none", color: "#000" }}
                        >
                          <img
                            src={article.border_photo}
                            alt={article.title}
                            style={{
                              marginBottom: 20,
                              borderRadius: 6,
                              objectFit: "cover",
                              width: "100%",
                              height: "216px",
                            }}
                          />
                          <Typography
                            variant="h6"
                            sx={{
                              marginBottom: "13px",
                              fontSize: { md: "20px", xs: "18px" },
                            }}
                          >
                            {article.title}
                          </Typography>
                          <Typography
                            variant="body2"
                            fontWeight={300}
                            sx={{
                              marginBottom: "15px",
                              /*height: { md: "44px", sm: "60px", xs: "40px" }*/
                              overflow: "hidden",
                              fontSize: { md: "16px", xs: "14px" },
                            }}
                            dangerouslySetInnerHTML={{
                              __html:
                                article.description.substring(0, 100) + "...",
                            }}
                          />
                          <Grid
                            container
                            alignItems="center"
                            justifyContent="space-between"
                          >
                            <Typography
                              variant="btnPrimaryOval"
                              sx={{
                                padding: {
                                  md: "5px 21px",
                                  sm: "4px 21px",
                                  xs: "5px 21px",
                                },
                                fontSize: {
                                  md: "16px",
                                  sm: "14px",
                                  xs: "16px",
                                },
                              }}
                            >
                              {article.category}
                            </Typography>
                            <Typography
                              variant="body2"
                              fontWeight={600}
                              sx={{
                                fontSize: {
                                  md: "16px",
                                  sm: "14px",
                                  xs: "16px",
                                },
                              }}
                            >
                              {new Date(article.create_at).toLocaleDateString()}
                            </Typography>
                          </Grid>
                        </Link>
                      </Grid>
                    )))}
          </Grid>
          {newsLength >= 10 && (
            <Button
            
              variant="btnPrimaryRect"
              onClick={handleShowMore}
              size="unset"
              sx={{
                marginTop: "27px",
                width: { md: "230px", sm: "290px", xs: "335px" },
              }}
            >
              <span style={{ borderBottom: "1px dashed #000000" }}>
                {!showMore && `Показать ещё ${newsLength - 9}`}
                {showMore && `Скрыть ${newsLength - 9}`}
              </span>
            </Button>
          )}
        </Container>
      </Box>
    </>
  );
};
export default News;
